/* width */
::-webkit-scrollbar {
	display: none;
	/* width: 10px; */
}
/* Track */
::-webkit-scrollbar-track {
  opacity: 0;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5)
}


.ant-collapse-content-box {
	padding-top: 0 !important;
}